<template>
  <div
    v-if="forms !== undefined && categories !== undefined"
    id="responses"
    class="box"
    style="overflow-x: auto;"
  >
    <table class="table is-striped is-hoverable is-fullwidth">
      <caption class="title is-scrollable">
        Formulários da Plataforma
      </caption>
      <thead>
        <tr>
          <th>
            Formulário
          </th>
          <th
            align="center"
            colspan="2"
          >
            Respostas
          </th>
          <th align="center">
            Tipo
          </th>
          <th align="center">
            Categoria
          </th>
          <th align="center">
            Ativo
          </th>
          <th />
          <th />
          <th />
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="form in forms"
          :key="form.id"
        >
          <td>
            {{ form.title }}
          </td>
          <td
            align="right"
            class="subtitle"
          >
            <a
              v-if="form.responses > 0"
              class=" flex row vca ra
              "
              :href="'responses/'+form.id"
            >
              {{ form.responses }}
            </a>
            <span v-else>
              {{ form.responses }}
            </span>
          </td>
          <td align="left">
            <a
              v-if="form.responses > 0"
              :href="'responses/'+form.id"
            >
              <span
                class="icon flex row vca"
                style="font-size: 1.2em;"
              >
                <i class="fas fa-eye" />
              </span>
            </a>
          </td>
          <td align="right">
            <div class="select is-rounded is-fullwidth">
              <select
                v-model.number="form.type"
                style="color : black;"
                :disabled="form.signup_uses > 0 || form.feedback_uses > 0 || form.responses > 0 || (form.category && categories[form.category].formproposalid == form.id)"
                @change="changeType(form.id, form.type == '' ? null : form.type)"
              >
                <option
                  v-for="(type, key) in types"
                  :key="key"
                  :value="key"
                  style="background-color: white; color: black;"
                >
                  {{ type.name }}
                </option>
              </select>
            </div>
          </td>
          <td align="right">
            <div
              v-if="form.type==2"
              class="select is-rounded is-fullwidth"
            >
              <select
                v-model.number="form.category"
                style="color : black;"
                :disabled="form.responses > 0 || (form.category && categories[form.category].formproposalid == form.id)"
                @change="changeCategory(form.id, form.category == '' ? null : form.category)"
              >
                <option disabled />
                <option
                  v-for="(category, key) in categories"
                  :key="key"
                  :value="key"
                  style="background-color: white; color: black;"
                >
                  {{ category.name }}
                </option>
              </select>
            </div>
          </td>
          <td align="center">
            <!-- <Switch 
              v-if="form.type==1" 
              :model-value="form.category && categories[form.category].formsignupid == form.id"
              :disabled="form.category && categories[form.category].formsignupid == form.id"
              :loading="form.loading"
              @update:modelValue="form.loading=true; changeActive(form.category, form.id, form.type, form)"
            /> -->
            <Switch 
              v-if="form.category && form.type==2" 
              :model-value="form.category && categories[form.category].formproposalid == form.id"
              :disabled="form.category && categories[form.category].formproposalid == form.id"
              :loading="form.loading"
              @update:modelValue="form.loading=true; changeActive(form.category, form.id, form.type, form)"
            />
            <!-- <Switch 
              v-else-if="form.type==3" 
              :model-value="form.category && categories[form.category].formfeedbackid == form.id"
              :disabled="form.category && categories[form.category].formfeedbackid == form.id"
              :loading="form.loading"
              @update:modelValue="form.loading=true; changeActive(form.category, form.id, form.type, form)"
            /> -->
          </td>
          <td align="center">
            <a :href="'duplicateForm/' + form.id">
              <i class="far fa-clone" />
            </a>
          </td>
          <td align="center">
            <a 
              :href="'createForm/' + form.id" 
            >
              <i class="fas fa-edit" />
            </a>
          </td>
          <td align="center">
            <a
              v-if="form.responses == 0 && !(form.category && categories[form.category].formproposalid == form.id)"
              href="#delete"
              @click="remove(form.id)"
            >
              <i class="fas fa-trash-alt" />
            </a>           
          </td>
        </tr>
        <tr>
          <td
            colspan="9"
            align="center"
            class="has-background-info-light"
          >
            <a
              class="flex row ca width-100"
              href="createForm"
            >Criar novo formulário</a>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Switch from "@/components/Switch"
import { API } from "@/http-common";
export default {
  name: "CreateResponse",
  components : {Switch},
  data() {
    return {
      forms: undefined,
      categories : undefined,
      types : undefined
    };
  },
  created() {
    Promise.all([
      API.get("formType/")
        .then((response) => this.types = response.data),
      API.get("form/")
        .then((response) => this.forms = response.data),
      API.get("eventType/")
        .then((response) => this.categories = response.data)])
      .then(() => {
        this.loading = false;
      })
  },
  methods: {
    remove(id) {
      if(confirm('De certeza que quer eliminar este formulário?')) {
        this.forms = this.forms.filter((x) => (x.id != id));
        const options = {
          method: "delete",
          url: "/form/" + id + "/",
          headers: {
            "X-CSRFToken": this.getCookie("csrftoken"),
          },
        };
        API(options);
      }
    },
    changeCategory(answer, category) {
      const options = {
        method: "patch",
        url: "/form/" + answer + "/",
        headers: {
          "X-CSRFToken": this.getCookie("csrftoken"),
        },
        data: {
          "category": category
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    changeType(answer, type) {
      const options = {
        method: "patch",
        url: "/form/" + answer + "/",
        headers: {
          "X-CSRFToken": this.getCookie("csrftoken"),
        },
        data: {
          "type": type
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    changeActive(category, active, type, form) {
      var string = "Deseja ativar este formulário?";
      if (this.categories[category].formproposalid) {
        string = string.concat(" Já existe um formulário ativo para esta categoria.");
      }
      if (confirm(string)) {
        var dataSend;
        if (type == 1)
          dataSend = {"formsignupid" : active};
        else if (type == 2)
          dataSend = {"formproposalid" : active};
        else if (type == 3)
          dataSend = {"formfeedbackid" : active};
        const options = {
          method: "patch",
          url: "/eventType/" + category + "/",
          headers: {
            "X-CSRFToken": this.getCookie("csrftoken"),
          },
          data: dataSend,
          transformResponse: [
            (data) => {
              this.response = data;
              form.loading = undefined;
              return data;
            },
          ],
        };
        API(options).then(() => {API.get("eventType/")
        .then((response) => this.categories = response.data);});
      }
      else
        form.loading = undefined;
    },
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          // Does this cookie string begin with the name we want?
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
  },
};
</script>

<style scoped lang="scss">
  .noafter:after {
    content: '' !important; 
  }

  th, td, a {
    vertical-align: middle !important;
  }
</style>
